@import url('https://fonts.googleapis.com/css?family=Roboto+Slab:300|Roboto:300,400');

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #dedede;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  -webkit-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
}

.header{
  background-color: white;
  -webkit-box-shadow: 0 1px 4px 0 rgba(0,0,0,0.50);
          box-shadow: 0 1px 4px 0 rgba(0,0,0,0.50);
  /* width: 100%; */
  min-width: 360px;
  height: 60px;
  margin: 0;
}

.header .content{
  margin: auto;
  height:100%;
  max-width: 1440px;
  display: -ms-flexbox;
  display: flex;
}

.body{
  width: 100%;
}

.body .content{
  margin:auto;
  padding: 10px;
  padding-top:15px;
  min-width: 360px;
  max-width: 1440px;
  display: -ms-flexbox;
  display: flex;
}

.body .content .main{
  width: 100%;
  margin: right;
  padding-right: 5px;
}

.body .content .sidebar{
  padding-left:5px;
  width: 300px;
  margin: left;
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .body .content .sidebar{
    display: none;
  }

}


