 .post-info-wrapper{
    display:-ms-flexbox;
    display:flex;
}

 .post-info-wrapper .avatar{
    padding: 20px;
}

 .post-info-wrapper .avatar img{
    height:50px;
}

 .post-info-wrapper .post-info{
    display: inline-block;
    padding: 25px 15px;
}

 .post-info-wrapper .post-info .username{
    color: black;
    font-size: 1rem;

}

 .post-info-wrapper .post-info .post-time{
    font-size: 0.8rem;
    color : #676767
}

 .post-info-wrapper .like-btn{
    margin-left: auto;
    margin-right: 60px;
    padding: 30px 0;
}

 .post-info-wrapper .like-btn .btn{
    font-size: 1.2rem;
}

/* For Liked button */
 .post-info-wrapper .like-btn .btn.true{
    color:red;
}

/* For not liked yet button */
 .post-info-wrapper .like-btn .btn.false{
    color:black;
}

 .post-content-wrapper{
    padding: 20px 50px;
    font-size:0.9rem;
    text-align: justify;
}




